import React from "react";
import Common from "./common.jsx";
import dog from "../images/dog.jpg";
const About = () =>{

    return(
        <>
            <Common
                name="Welcome to About Us"
                title="Lorem ipsum dolor site amet"
                imgsrc={dog}
                visit="/contactus"
                btnname="Contact Us"

            />

            <table class="table">
            <thead>
                <tr>
                <th scope="col">#</th>
                <th scope="col">First</th>
                <th scope="col">Last</th>
                <th scope="col">Handle</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                <th scope="row">1</th>
                <td>Mark</td>
                <td>Otto</td>
                <td>@mdo</td>
                </tr>
                <tr>
                <th scope="row">2</th>
                <td>Jacob</td>
                <td>Thornton</td>
                <td>@fat</td>
                </tr>
                <tr>
                <th scope="row">3</th>
                <td colspan="2">Larry the Bird</td>
                <td>@twitter</td>
                </tr>
            </tbody>
            </table>
        </>
    );
}
export default About;