import React, { useState } from "react";

const ContactUs = () =>{
    const [data, SetData] =useState({
        fname:"",
        phone:"",
        email:"",
        message:""
    });

    const InputEvent = (event) =>{
        const {name,value} = event.target;

        SetData((oldval)=>{
            return{
                ...oldval,
                [name] : value,
            };
        })
    }

    const submit= (e) =>{
        e.preventDefault();
        alert(
            `Name is ${data.fname}.
            phone is ${data.phone}.
            email is ${data.email}.
            message is ${data.message}.`
        );
    }
    return(
        <>
            <div className="row">
            <h2 className="my-5" style={{textAlign: "center"}}>Contact Us Form</h2>
                <div className="col-lg-3 mx-auto">
                <form onSubmit={submit}>

                    <div className="mb-3">
                    <label for="fname" className="form-label">Full Name</label>
                    <input name="fname" value={data.fname} type="fname" className="form-control" id="fname" aria-describedby="fname" onChange={InputEvent}/>
                    </div>

                    <div className="mb-3">
                    <label for="phone" className="form-label">Phone</label>
                    <input name="phone" value={data.phone} type="phone" className="form-control" id="phone" aria-describedby="phone" onChange={InputEvent}/>
                    </div>


                    <div className="mb-3">
                    <label for="email" className="form-label">Email address</label>
                    <input name="email" value={data.email} type="email" className="form-control" id="email" placeholder="name@example.com" onChange={InputEvent}/>
                    </div>


                    <div className="mb-3">
                    <label for="message" className="form-label">Message</label>
                    <textarea name="message" value={data.message} className="form-control" id="message" rows="3" onChange={InputEvent}></textarea>
                    </div>

                    <button type="submit" className="btn btn-primary">Submit</button>
                    </form>
                </div>
            </div>
            


            
        </>
    );
}
export default ContactUs;