import React from "react";
import Card from "../AnimatedWebsite/Card";
import Sdata from "./Sdata.jsx";
const Services = () =>{
    return(
        <>

            {Sdata.map((val,index)=>{
                return(
                    <Card
                        key={index}
                        imgsrc={val.imgsrc}
                        title={val.title}
                    />
                );
            })}
           
        </>
    );
}
export default Services;