import React from "react";
import {Redirect, Switch, Route } from "react-router-dom";
import Navbar from "./AnimatedWebsite/Navbar.jsx";
import Home from "./AnimatedWebsite/Home.jsx";
import About from "./AnimatedWebsite/About.jsx";
import Services from "./AnimatedWebsite/Services.jsx";
import ContactUs from "./AnimatedWebsite/ContactUs.jsx";
import Footer from "./AnimatedWebsite/Footer.jsx";
import "./AnimatedWebsite/style.css";
const App = () =>{
    return(
        <>
        <Navbar/>
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                
                </div>
                <div className="col-lg-12">
                <div className="row">
                <Switch>
                    <Route exact path="/"  component={()=> <Home name="Home"/>}/>
                    <Route exact path="/home"  component={()=> <Home name="Home"/>}/>
                    <Route exact path="/about"  component={()=> <About name="About"/>}/>
                    <Route exact path="/services"  component={()=> <Services name="Services"/>}/>
                    <Route exact path="/contactus"  component={()=> <ContactUs name="ContactUs"/>}/>
                    <Redirect exact to="/"></Redirect>
                </Switch>
                </div>
                </div>
               
            </div>
               
        </div>
        <Footer/>
        </>
    );
}


export default App;